<template>
  <div class="lwh_cont">
    <div class="head">
      <p>
        ele.me
        <span :class="{ dis: !disp }" @click="add()">登录</span>
        <span :class="{ dis: disp }" @click="ads()">
          <van-icon name="user-circle-o" />
        </span>
      </p>
      <div class="sl">
        <a
          v-for="(value, key, index) in list_cont"
          :key="index"
          @click="but(index)"
          >{{ key }}</a
        >
      </div>
      <div class="top" :class="{ flags: flag }" @click="btn">返回顶部</div>
    </div>

    <div class="bs">
      <div>
        <!-- 当前城市 -->
        <div class="list_tit">
          <p>
            <span>当前城市</span>
            <span>当前城市不准时在下方选择</span>
          </p>
          <p>
            <span @click="cs(1)">{{ list_tit.name }}</span>
            <span>></span>
          </p>
        </div>
        <p class="g"></p>
        <!-- 热门城市 -->
        <div class="list_hot">
          <p>热门城市</p>
          <ul>
            <li v-for="item in list_hot" :key="item.id" @click="cs(item)">
              {{ item.name }}
            </li>
          </ul>
        </div>
        <p class="g"></p>
        <!-- 城市选择 -->
        <div
          class="list_cont"
          v-for="(value, key, index) in list_cont"
          :key="index"
          :id="key"
        >
          <p>{{ key }}</p>
          <ul>
            <li v-for="(item, indexs) in value" :key="indexs" @click="cs(item)">
              {{ item.name }}
            </li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import BScroll from "better-scroll";
import { mapState } from "vuex";
import { ajaxcs } from "../utils/res";

export default {
  name: "",
  // 组件注册
  components: {},
  // 父组件传参
  props: {},
  // 变量声明
  data() {
    return {
      bs: null,

      list_tit: {},
      list_hot: null,
      list_cont: {},
      flag: false,
    };
  },
  // Vue实例创建完成以后执行 数据可用
  created() {},
  // Vue模板挂载完毕以后执行  DOM节点加载完毕了（可以获取节点了）
  mounted() {
    this.$nextTick(() => {
      setTimeout(() => {
        this.bs = new BScroll(".bs", {
          probeType: 3,
          click: true,
        });
        this.bs.on("scroll", (pos) => {
          if (pos.y < -50) {
            this.flag = true;
          } else {
            this.flag = false;
          }
        });
      }, 800);
    }),
      this.tit(),
      this.hot(),
      this.cont();
  },
  // vue组件更新完毕以后执行
  update() {},
  // 定义事件
  methods: {
    // onScroll(pos) {
    // }
    async tit() {
      let { data } = await ajaxcs("http://elm.cangdu.org/v1/cities?type=guess");

      this.list_tit = data;
      console.log(this.list_tit);
    },
    async hot() {
      let { data } = await this.axios.get(
        "http://elm.cangdu.org/v1/cities?type=hot"
      );
      this.list_hot = data;
    },
    add() {
      this.$router.push("/add");
    },
    ads() {
      this.$router.push("/sy/gr");
    },
    btn() {
      this.bs.scrollTo(0, 0, 1000);
    },
    but(index) {
      let add = document.querySelectorAll(".list_cont");
      let a = Array.from(add).slice(0, index);
      let b = 0;
      a.map((i) => {
        b += i.offsetHeight;
      });
      b = b + (192 - 30);
      this.bs.scrollTo(0, -b, 1000);
    },
    async cont() {
      let { data } = await this.axios.get(
        "http://elm.cangdu.org/v1/cities?type=group"
      );

      Object.keys(data)
        .sort()
        .map((key) => {
          this.list_cont[key] = data[key];
        });
    },
    cs(val) {
      if (val == 1) {
        this.$router.push({ path: "/cs", query: { cs: this.list_tit } });
        this.$store.commit("cs",this.list_tit)
      } else {
        this.$router.push({ path: "/cs", query: { cs: val } });
        this.$store.commit("cs",val)
      }
      
    },
  },
  // 过滤器
  filters: {
    // lwh_fro(val,cont){
    //     function dateFormat(date, format) {
    //             if (typeof date === "string") {
    //                 var mts = date.match(/(\/Date\((\d+)\)\/)/);
    //                 if (mts && mts.length >= 3) {
    //                     date = parseInt(mts[2]);
    //                 }
    //             }
    //             date = new Date(date);
    //             if (!date || date.toUTCString() == "Invalid Date") {
    //                 return "";
    //             }
    //             var map = {
    //                 "M": date.getMonth() + 1, //月份
    //                 "d": date.getDate(), //日
    //                 "h": date.getHours(), //小时
    //                 "m": date.getMinutes(), //分
    //                 "s": date.getSeconds(), //秒
    //                 "q": Math.floor((date.getMonth() + 3) / 3), //季度
    //                 "S": date.getMilliseconds() //毫秒
    //             };
    //             format = format.replace(/([yMdhmsqS])+/g, function (all, t) {
    //                 var v = map[t];
    //                 if (v !== undefined) {
    //                     if (all.length > 1) {
    //                         v = '0' + v;
    //                         v = v.substr(v.length - 2);
    //                     }
    //                     return v;
    //                 } else if (t === 'y') {
    //                     return (date.getFullYear() + '').substr(4 - all.length);
    //                 }
    //                 return all;
    //             });
    //             return format;
    //         }
    //         return dateFormat(val,cont)
    // }
  },
  // 计算属性
  computed: {
    ...mapState(["disp"]),
  },
  // 监听属性
  watch: {},
};
</script>

<style lang="scss" scoped>
.lwh_cont {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  > div:nth-of-type(1) {
    width: 100%;
    height: 0.5rem;
    background:#007aff;
    > p {
      height: 0.5rem;
      color: #fff;
      font-size: 0.3rem;
      display: flex;
      box-sizing: border-box;
      padding: 0 0.1rem;
      justify-content: space-between;
      align-items: center;
      > span {
        font-size: 0.2rem;
        display: none;
      }
      > .dis {
        display: block;
      }
    }
  }
  > div:nth-of-type(2) {
    width: 100%;
    flex: 1;
    overflow: hidden;

    > div {
      width: 100%;
      min-height: 101%;
      
    }
  }
}
.list_tit {
  width: 100%;
  height: 0.8rem;
  background: #fff;
  > p {
    width: 100%;
    height: 0.4rem;
    border-bottom: 0.01rem solid #eee;
    display: flex;
    box-sizing: border-box;
    padding: 0 0.05rem;
    justify-content: space-between;
    align-items: center;
  }
  > p:nth-of-type(2) {
    font-size: 0.2rem;
    > span:nth-of-type(1) {
      color:#007aff;
    }
  }
}
.list_hot {
  width: 100%;
  background: #fff;
  > p {
    width: 100%;
    height: 0.4rem;
    line-height: 0.4rem;
  }
  > ul {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    > li {
      width: 25%;
      height: 0.3rem;
      text-align: center;
      color: #007aff;
    }
  }
}
.g {
  width: 100%;
  height: 0.05rem;
  background: #eee;
}
.list_cont {
  width: 100%;
  background: #fff;
  > p {
    width: 100%;
    height: 0.4rem;
    line-height: 0.4rem;
    font-size: 0.2rem;
    background: #eee;
    color: #007aff;
  }

  > ul {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    > li {
      width: 25%;
      height: 0.3rem;
      text-align: center;
      color: #666;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      box-sizing: border-box;
      
    }
  }
}
.top {
  width: 100%;
  height: 0.3rem;
  line-height: 0.3rem;
  text-align: center;
  position: fixed;
  top: 0.5rem;
  left: 0;
  background: rgb(255, 115, 0);
  z-index: 1000;
  display: none;
  color: #fff;
}
.flags {
  display: block;
}
.sl {
  width: 0.2rem;
  display: flex;
  flex-direction: column;
  position: fixed;
  top: 0.9rem;
  right: 0;
  z-index: 1000;
  background: #fff;
  > a {
    display: block;
    text-align: center;
  }
}
</style>
