<template>
  <div class="dl">
    <div class="head">
      <span @click="add()">＜</span>
      <span>密码登录</span>
    </div>
    <div class="body">
      <div>
        <input
          type="text"
          placeholder="账号"
          maxlength="16"
          v-model="form.zh"
        />
      </div>
      <div>
        <input
          :type="types"
          placeholder="密码"
          maxlength="16"
          v-model="form.mm"
        />
        <div :class="{ q: true, qs: flag }">
          <div>
            <p></p>
            <p @click="btn()"></p>
          </div>
        </div>
      </div>
      <div>
        <input type="text" placeholder="验证码" v-model="form.yz" />
        <div class="y">
          <img :src="img" alt="" />
          <p>
            <span>看不清</span>
            <br />
            <span @click="app()">换一张</span>
          </p>
        </div>
      </div>

      <div class="foot">
        <p>温馨提示：未注册过的账号</p>
        <p>登录时将自动注册注册过的用户可凭账号密码登录</p>
        <button :disabled="flags" @click="dl()">登录</button>
        <a href="#">重置密码?</a>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "",
  // 组件注册
  components: {},
  // 父组件传参
  props: {},
  // 变量声明
  data() {
    return {
      types: "password",
      flag: false,
      img: null,
      form: {
        zh: 17612808410,
        mm: "123456789",
        yz: null,
      },
      flags: true,
    };
  },
  // Vue实例创建完成以后执行 数据可用
  created() {},
  // Vue模板挂载完毕以后执行  DOM节点加载完毕了（可以获取节点了）
  mounted() {
    this.imgs();
  },
  // vue组件更新完毕以后执行
  update() {},
  // 定义事件
  methods: {
    add() {
      this.$router.push("/cont");
    },
    btn() {
      this.flag = !this.flag;
      if (this.flag == true) {
        this.types = "text";
      } else {
        this.types = "password";
      }
    },
    async imgs() {
      let { data } = await this.axios.post("http://elm.cangdu.org/v1/captchas");
      this.img = data.code;
      console.log(data);
    },
    app() {
      this.imgs();
    },
     dl() {
      // let a = /^((13[0-9])|(14[5|7])|(15([0-3]|[5-9]))|(18[0,5-9]))\d{8}$/;
      // if (!a.test(Number(this.form.zh))) {
      //   alert("不是完整的11位手机号或者正确的手机号前七位");
      //   return false;
      // }

      // if (
      //   !/^(?=.*[a-z])(?=.*[A-Z])[a-zA-Z0-9~!@&%#_]{8,16}$/.test(this.form.mm)
      // ) {
      //   alert("必须包含一个大写，一个小写字母，且长度为8到16位");
      //   return false;
      // }
      
      this.setCookie("cap", this.form.yz, 2);
      this.setCookie("SID", 200, 2);
      axios.defaults.withCredentials = true;
      
       axios
        .post("/api/v2/login", {
          username: this.form.zh,
          password: this.form.mm,
          captcha_code: this.form.yz,
        })
        .then((data) => {
          console.log(data);
          if (data.status == 200) {
            this.$router.push( "/sy/gr");
            
            this.$store.commit("but",data.data);
          }
        });
        // if (data.status == 200) {
        //     this.$router.push({ path: "/gr", query:  data  });
        //     this.$store.commit("but");
        //   }
        // console.log(data)
    },
    setCookie(key, value, t) {
      var oDate = new Date();
      oDate.setDate(oDate.getDate() + t);
      document.cookie = key + "=" + value + "; expires=" + oDate.toDateString();
    },
    // setCookie(key, value, t) {
    //   var oDate = new Date();
    //   oDate.setDate(oDate.getDate() + t);
    //   document.cookie = key + "=" + value + "; expires=" + oDate.toDateString();
    // },
  },
  // 过滤器
  filter: {},
  // 计算属性
  computed: {},
  // 监听属性
  watch: {
    form: {
      handler(val) {
        if (val.zh != "" && val.mm != "" && val.yz != "") {
          this.flags = false;
        } else {
          this.flags = true;
        }
      },
      deep: true,
    },
  },
};
</script>

<style lang="scss" scoped>
.dl {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
}
.head {
  width: 100%;
  height: 0.5rem;
  background: #007aff;
  color: #fff;
  display: flex;
  font-size: 0.2rem;
  align-items: center;
  > span:nth-of-type(1) {
    width: 0.2rem;
    text-align: center;
  }
  > span:nth-of-type(2) {
    flex: 1;
    text-align: center;
  }
}
.body {
  width: 100%;
  flex: 1;
  background: #eee;
  box-sizing: border-box;
  padding-top: 0.2rem;
  > div {
    position: relative;
    > input {
      width: 100%;
      height: 0.5rem;
      border: none;
      outline: none;
      margin-bottom: 0.01rem;
      text-indent: 0.2rem;
    }
  }
}
.y {
  width: 1rem;
  height: 0.5rem;
  display: flex;
  position: absolute;
  top: 0;
  right: 0.5rem;
  > img {
    width: 0.6rem;
    height: 0.5rem;
  }
  > p {
    font-size: 0.12rem;
    > span {
      // margin-bottom: 0.1rem;
      display: block;
    }
  }
}
.q {
  position: absolute;
  top: 0.2rem;
  right: 0.5rem;
  > div {
    position: relative;
    > p:nth-of-type(1) {
      width: 0.3rem;
      height: 0.1rem;
      border-radius: 0.1rem;
      background: #eee;
      transition: background-color 0.2s;
    }
    > p:nth-of-type(2) {
      position: absolute;
      top: -25%;
      left: 0;
      width: 0.15rem;
      height: 0.15rem;
      border-radius: 50%;
      background: red;
      transition: left 0.2s;
    }
  }
}
.qs {
  > div {
    > p:nth-of-type(1) {
      background: rgb(60, 255, 0);
      transition: all 0.2s;
    }
    > p:nth-of-type(2) {
      left: 0.15rem;
      transition: all 0.2s;
    }
  }
}
.foot {
  width: 100%;
  box-sizing: border-box;
  padding: 0 0.2rem;
  > p {
    width: 100%;
    height: 0.3rem;
    line-height: 0.3rem;
    color: red;
  }
  > button {
    width: 100%;
    height: 0.4rem;
    border-radius: 0.05rem;
    border: none;
    color: #fff;
    background: rgb(60, 255, 0);
    margin-bottom: 0.2rem;
  }

  button[disabled] {
    color: white !important;
    background: rgba(60, 255, 0, 0.4) !important;
  }
  > a {
    float: right;
  }
}
</style>
