<template>
  <div class="sy">
    
    <router-view class="st"></router-view>
    <div >
      <van-tabbar v-model="actives">
        <van-tabbar-item v-for="(item,index) in list" :key="index" @click="btn(item,index)" :icon="item.icon">{{item.cont}}</van-tabbar-item>
      </van-tabbar>
    </div>
  </div>
</template>

<script>
import {mapState} from 'vuex'
export default {
  name: "",
  // 组件注册
  components: {
     
  },
  // 父组件传参
  props: {},
  // 变量声明
  data() {
    return {
        
         list:[
          {icon:"home-o",cont:"首页",path:"/sy/sy"},
          {icon:"search",cont:"搜索",path:"/sy/ss"},
          {icon:"setting-o",cont:"订单",path:"/sy/dd"},
          {icon:"friends-o",cont:"个人",path:"/sy/gr"},
      ],
      actives:0
      
    };
  },
  // Vue实例创建完成以后执行 数据可用
  created() {
    this.actives= this.active
   
  },
  // Vue模板挂载完毕以后执行  DOM节点加载完毕了（可以获取节点了）
  mounted() {
    //  this.$nextTick(()=>{
    //   console.log(this.$store.state.active)
    //   this.active = this.$store.state.active
    // })
    // this.$set(this.active,0,this.$store.state.active)
    // console.log(this.active)
  },
  // vue组件更新完毕以后执行
  update() {},
  // 定义事件
  methods: {
      btn(val,index){
          this.$router.push(val.path)
          this.$store.commit("activ",index)
      }
  },
  // 过滤器
  filter: {},
  // 计算属性
  computed: {
    ...mapState(["active"])
  },
  // 监听属性
  watch: {
   active(v){
     this.actives=v
   }
  },
};
</script>

<style lang="scss" scoped>

.sy{
    width: 100%;
    height: 100%;
    display: flex;
    // flex-direction: column;
}
.st{
  flex: 1;
}

</style>
