<template>
  <div class="cs">
    <div class="head">
      <span @click="add()">＜</span>
      <span>{{ tit.name }}</span>
    </div>
    <div class="ss">
      <input
        type="text"
        v-model="cont"
        placeholder="搜索地址"
        @keyup.enter="ju()"
      />
      <span class="s" v-show="flags" @click="q()">x</span>
      <button @click="ju()">搜索</button>
    </div>
    <div class="ls" v-show="!flag">
      <p>搜索历史</p>
      <ul class="list">
        <li v-for="(item, index) in lisx" :key="index" @click="btn(item)">
          <p>{{ item.name }}</p>
          <p>{{ item.address }}</p>
        </li>
      </ul>
    </div>
    <ul class="list" v-show="flag">
      <li v-for="(item, index) in list" :key="index" @click="btn(item)">
        <p>{{ item.name }}</p>
        <p>{{ item.address }}</p>
      </li>
    </ul>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  name: "",
  // 组件注册
  components: {},
  // 父组件传参
  props: {},
  // 变量声明
  data() {
    return {
      cont: "",
      list: [],
      flag: false,
      flags: false,
    };
  },
  // Vue实例创建完成以后执行 数据可用
  created() {},
  // Vue模板挂载完毕以后执行  DOM节点加载完毕了（可以获取节点了）
  mounted() {},
  // vue组件更新完毕以后执行
  update() {},
  // 定义事件
  methods: {
    add() {
      this.$router.push("/cont");
    },
    async ju() {
      if (this.cont != "") {
        console.log(1);
        let { data } = await this.axios.get(
          `
        http://elm.cangdu.org/v1/pois`,
          {
            params: {
              type: "search",
              city_id: this.tit.id,
              keyword: this.cont,
            },
          }
        );
        // this.$set(this.list,data)
        this.list = data;
        console.log(this.list);
        this.flag = true;
      }
    },
    btn(val) {
      this.$router.push({path:"/sy/sy",query:{val:val.name}})
      this.flag = false;
      this.$store.commit("lisx", val);
    },
    q() {
      this.cont = "";
    },
  },
  // 过滤器
  filter: {},
  // 计算属性
  computed: {
    ...mapState(["tit", "lisx"]),
  },
  // 监听属性
  watch: {
    cont(val) {
      if (val != "") {
        this.flags = true;
      } else {
        this.flags = false;
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.cs {
  width: 100%;
  height: 100%;
  background: #eee;
}
.head {
  width: 100%;
  height: 0.5rem;
  background: #007aff;
  color: #fff;
  display: flex;
  font-size: 0.2rem;
  align-items: center;
  > span:nth-of-type(1) {
    width: 0.2rem;
    text-align: center;
  }
  > span:nth-of-type(2) {
    flex: 1;
    text-align: center;
  }
}
.ss {
  width: 100%;
  height: 1rem;
  background: #fff;
  box-sizing: border-box;
  padding: 0.1rem;
  margin: 0.2rem 0 0.01rem 0;

  text-align: center;
  > input {
    width: 99%;
    height: 0.3rem;
    
    text-indent: 0.05rem;
  }
  > button {
    width: 100%;
    height: 0.3rem;
    margin-top: 0.1rem;
    border: none;
    background: #007aff;
    color: #fff;
    border-radius: 0.05rem;
  }
}
.ls {
  width: 100%;
  > p {
    width: 100%;
    border-bottom: 1px solid #000;
  }
}
.list {
  width: 100%;
  > li {
    width: 100%;
    height: 0.8rem;
    box-sizing: border-box;
    padding: 0.1rem;
    background: #fff;
    margin-bottom: 0.01rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    > p:nth-of-type(2) {
      color: #666;
    }
  }
}
.lisl {
  width: 100%;
  > li {
    width: 100%;
    height: 0.3rem;
    box-sizing: border-box;
    padding: 0.1rem;
    background: #fff;
    margin-bottom: 0.01rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
}
.s {
  position: fixed;
  top: 0.87rem;
  right: 0.3rem;
}
</style>
