<template>
  <div class="ssy">
    <v-head>
      <span @click="add()" slot="left">
        <van-icon name="search" />
      </span>
      <span slot="tit">{{ tits }}</span>
      <span slot="rig" @click="ads()" >
        <van-icon name="user-circle-o" />
      </span>
    </v-head>
    <div class="ssy_body">
      <div>
        <div class="swip">
          <van-swipe class="my-swipe" :autoplay="3000" indicator-color="#007aff">
            <van-swipe-item>
              <ul>
                <li
                  v-for="(item, index) in list"
                  :key="index"
                  v-show="index < 8"
                >
                  <img
                    :src="'https://fuss10.elemecdn.com' + item.image_url"
                    alt=""
                  />
                  <p>{{ item.title }}</p>
                </li>
              </ul>
            </van-swipe-item>
            <van-swipe-item>
              <ul>
                <li
                  v-for="(item, index) in list"
                  :key="index"
                  v-show="index >= 8"
                >
                  <img
                    :src="'https://fuss10.elemecdn.com' + item.image_url"
                    alt=""
                  />
                  <p>{{ item.title }}</p>
                </li>
              </ul>
            </van-swipe-item>
          </van-swipe>
        </div>
        <div class="list">
          <div class="list_tit">附近商家</div>
          <div class="cont"  v-for="(item, index) in lists" :key="index">
            <img
              :src="'https://elm.cangdu.org/img/' + item.image_path"
              alt=""
            />
            <div class="bann">
              <p>
                <span>品牌</span>

                <span>{{item.name}}</span>
              </p>
              <div>
                <div>
                  <span id="sp"
                    ><van-rate
                      v-model="item.rating"
                      :size="12"
                      color="#ffd21e"
                      void-icon="star"
                      void-color="#eee"
                  /></span>
                  <span>{{item.rating}}</span>
                  <span>月销{{item.recent_order_num}}单</span>
                </div>
                <div>
                  <span>蜂鸟专送</span>
                  <span>准时达</span>
                </div>
              </div>
              <div>
                <span>￥{{item.float_minimum_order_amount}}起送/配送费约￥{{item.float_delivery_fee}}</span>

                <p>{{item.distance}}/<span>{{item.order_lead_time}}</span></p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="foot">

    </div>
  </div>
</template>

<script>
import BScroll from "better-scroll";
import VHead from "./head";
import { mapState } from "vuex";
export default {
  name: "",
  // 组件注册
  components: {
    VHead,
  },
  // 父组件传参
  props: {},
  // 变量声明
  data() {
    return {
      list: [],
      lists: [],
      value: 1.5,
    };
  },
  // Vue实例创建完成以后执行 数据可用
  created() {},
  // Vue模板挂载完毕以后执行  DOM节点加载完毕了（可以获取节点了）
  mounted() {
    this.$nextTick(() => {
      setTimeout(() => {
        this.bs = new BScroll(".ssy_body", {
          probeType: 3,
          click: true,
        });
      }, 800);
    }),
      this.but(),
      this.buts();
      this.$store.commit("activ",0);

  },
  // vue组件更新完毕以后执行
  update() {},
  // 定义事件
  methods: {
    async but() {
      let { data } = await this.axios.get(
        "http://elm.cangdu.org/v2/index_entry?geohash=32.046224,118.784582&group_type=1&flags[]=F"
      );
      this.list = data;
    },
    async buts() {
      let { data } = await this.axios.get(
        "http://elm.cangdu.org/shopping/restaurants?latitude=31.125419&longitude=121.571386&aurant_category_ids[]=&order_by=&delivery_mode[]="
      );
      this.lists = data;
      console.log(data);
    },
    add(){
      this.$router.push("/sy/ss")
      // this.$store.commit("activ",1)
    },
    ads(){
      this.$router.push("/sy/gr")
      // this.$store.commit("activ",3)
    }
  },
  // 过滤器
  filter: {},
  // 计算属性
  computed: {
    ...mapState(["tits"]),
  },
  // 监听属性
  watch: {},
};
</script>

<style lang="scss" scoped>
.ssy {
  width: 100%;
  
  display: flex;
  flex-direction: column;
  > .ssy_body {
    width: 100%;
    flex: 1;
    overflow: hidden;
    > div {
      width: 100%;
      min-height: 101%;
      background: #eee;
    }
  }
}
.my-swipe .van-swipe-item {
  color: #000;
  font-size: 0.16rem;

  text-align: center;
}
.swip {
  margin-bottom: 0.2rem;
  background: #fff;
  ul {
    width: 100%;
    height: 2rem;
    box-sizing: border-box;
    padding: 0, 0, 0.4rem, 0;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    > li {
      width: 25%;
      height: 0.8rem;
      text-align: center;
      > img {
        height: 0.5rem;
      }
    }
  }
}
.list {
  width: 100%;
  background: #fff;
  > .list_tit {
    width: 100%;
    height: 0.3rem;
    line-height: 0.3rem;
    color: #999;
    text-indent: 0.1rem;
  }
}
.cont {
  width: 100%;
  height: 1.1rem;
  box-sizing: border-box;
  padding: 0.15rem 0.1rem;
  display: flex;
  border:0.01rem solid #eee;
  > img {
    width: 0.8rem;
  }

  > div {
    flex: 1;
    box-sizing: border-box;
    padding-left: 0.1rem;
    > p {
      width: 100%;
      height: 0.3rem;
      display: flex;
      align-items: center;
      font-size: 0.18rem;
      > span:nth-of-type(1) {
        background: orange;
        font-size: 0.1rem;
        padding: 0.01rem;
        margin-right: 0.05rem;
      }
    }
    > div:nth-of-type(1) {
      height: 0.3rem;
      display: flex;
      align-items: center;
      justify-content: space-between;
      > div:nth-of-type(1) {
        > span:nth-of-type(2) {
          font-size: 0.1rem;
          color: orange;
          margin: 0 0.05rem;
        }
        > span:nth-of-type(3) {
          font-size: 0.1rem;
        }
      }
      > div:nth-of-type(2) {
        > span:nth-of-type(1) {
          background:#007aff;
          font-size: 0.05rem;
          padding: 0.01rem;
          color: #fff;
          margin-right: 0.05rem;
        }
        > span:nth-of-type(2) {
          color: #007aff;

          border: 0.01rem solid #007aff;
          font-size: 0.05rem;
        }
      }
    }
    > div:nth-of-type(2) {
      display: flex;
      justify-content: space-between;
      align-items: center;
      height: 0.2rem;
      > span:nth-of-type(1) {
        font-size: 0.12rem;
        color: #666;
      }
      > p {
        font-size: 0.12rem;
        color: #666;
        > span:nth-of-type(1) {
          color: #007aff;
        }
      }
    }
  }
}
.foot{
  height: 0.5rem;
}
</style>
